@include colors-generator();
@include mixed-colors-generator();

/* Responsive */
@media (max-width: 940px) {
  .container {
    flex-direction: column;
    margin-top: 60px;
  }

  .left-column {
    min-height: 440px;
  }
  .left-column,
  .right-column {
    width: 100%;
  }

  .left-column img {
    width: 300px;
    right: 0;
    top: -65px;
    left: initial;

    &.mendi-product {
      width: 530px;
    }
  }
}

@media (max-width: 535px) {
  .left-column img {
    width: 220px;
    top: -85px;
    &.mendi-product {
      width: 360px;
    }
  }
}
